import { BehaviorSubject, Observable, ReplaySubject, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';

import { AggregateId } from '../core/domain/tactical/aggregate/aggregate-id';
import { ReactiveService } from './reactive.service';
import { KeyMap } from './collections/key-map';
import { Optional } from './optional';

export interface DefaultAggregateValues<T> {
	aggregateId: AggregateId;
	value: T;
}

export abstract class AggregateArchive<T> extends ReactiveService {

	private readonly archive = new KeyMap<AggregateId, T>();

	private readonly archive$: Subject<KeyMap<AggregateId, T>>;

	protected constructor(defaultValue?: DefaultAggregateValues<T>) {
		super();
		if (defaultValue) {
			this.archive.set(defaultValue.aggregateId, defaultValue.value);
			this.archive$ = new BehaviorSubject<KeyMap<AggregateId, T>>(this.archive);
		} else {
			this.archive$ = new ReplaySubject<KeyMap<AggregateId, T>>(1);
		}
	}

	on(aggregateId: AggregateId): Observable<T> {
		return this.archive$
				   .asObservable()
				   .pipe(
					   filter(() => this.isNotStopped()),
					   map((map: KeyMap<AggregateId, T>) => {
						   return map.get(aggregateId);
					   }),
					   filter((value: Optional<T>) => value.isPresent()),
					   map((value: Optional<T>) => value.getValueOrNullOrThrowError()),
					   distinctUntilChanged(),
					   this.takeUntil()
				   );
	}

	get(aggregateId: AggregateId): Optional<T> {
		return this.archive.get(aggregateId);
	}

	next(aggregateId: AggregateId, value: T): void {
		this.archive.set(aggregateId, value);
		this.archive$.next(this.archive);
	}

}
