import { AggregateId } from '../../domain/tactical/aggregate/aggregate-id';
import { AggregateRoot } from '../../domain/tactical/aggregate/aggregate-root';
import { Optional } from '../../../common/optional';

export abstract class InMemoryStore<I extends AggregateId, A extends AggregateRoot<I>> {

	private readonly state: Map<string, A> = new Map();

	set(aggregate: A): void {
		this.state.set(aggregate.getId().toString(), aggregate);
	}

	setMany(aggregates: Array<A>): void {
		aggregates.forEach((aggregate: A) => {
			this.set(aggregate);
		});
	}

	get(aggregateId: I): Optional<A> {
		return Optional.of(this.state.get(aggregateId.toString()));
	}

	getAll(): Array<Optional<A>> {
		return Array.from(this.state.values()).map(v => Optional.of(v));
	}

	has(aggregateId: I): boolean {
		return this.state.has(aggregateId.toString());
	}

	delete(aggregateId: I): void {
		this.state.delete(aggregateId.toString());
	}

	clear(): void {
		this.state.clear();
	}

}
