import { AggregateId } from '../tactical/aggregate/aggregate-id';
import { Message } from '../message/message';


export abstract class DomainEvent<I extends AggregateId = AggregateId> extends Message<I> {

	private readonly payload: any;

	protected constructor(aggregateId: I,
						  payload: any,
						  messageType: string) {
		super(aggregateId, messageType);
		this.payload = payload;
	}

	isSameType(event: DomainEvent<I>): boolean {
		return this.constructor.name === event.constructor.name;
	}

	getPayload(): any {
		return this.payload;
	}
}
